import { Component, OnInit } from '@angular/core';
import { DataService } from '../../_services';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.scss']
})
export class MessagesComponent implements OnInit {
  oMessages: any = [];


  constructor(private oAPIService: DataService) { }

  ngOnInit(): void {
    this.oAPIService.send2ServerP("messages", true).then((data: any) => {
      this.oMessages = data.result;
      console.log(this.oMessages);
    });

  }
  public onDeleteMessage(oItem:any) {
    this.oAPIService.send2ServerP("messages/" + oItem.ID + "/delete", true).then((data: any) => {
      var index = this.oMessages.indexOf(oItem);
      if (index !== -1) this.oMessages.splice(index, 1);
    });


  }
}