import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-jsonform',
  templateUrl: './jsonform.component.html',
  styleUrls: ['./jsonform.component.scss']
})
export class JSONFormComponent implements OnInit {
  bShowSpinner = false;

  oFormSchema: any = {};
  oFormLayout: any = {};
  oFormData: any = {};

  constructor() { }

  ngOnInit(): void {
  }

  public loadJSONForm(formObject: any) {
    this.oFormSchema = formObject['schema'];
    this.oFormLayout = formObject['layout'];
    this.oFormData = formObject['data'];
    // this.oFormData['Owner'] = sessionStorage.getItem('Email');
  }

  public formOptions = {
    defautWidgetOptions: {
      returnEmptyFields: true
    }
  };
}
