import { Component, OnInit, OnDestroy } from '@angular/core';
import { Paho } from 'mosq-mqtt/mqttws31';
import { MqttService } from 'abstract-mqtt';
import { MQTT_HOST, MQTT_PORT, MQTT_USESSL } from '../../../env.properties';
import { AuthenticationService } from '../../../app/_services';
import { User } from '../../../app/_models';
// import { UUID } from 'angular2-uuid';
import { v4 as uuidv4 } from 'uuid';


var Global_Variables = {
  // iUserID: '',
  // sUserType: 'N',
  //  isConnected: true,
  // token: null,
  // // DeviceID: "None",
  // oEID: -1,
  // sEntityID: 0,
  //  oRItem: null,
  //  oMenuItems: [],
  // sCurrentPage: 'entities',
  // bSaved: false,
  // InstantQ: '',
  // sDeviceUUID: null,
  // sDeviceReg: null,
  //  sCountry: '',
  // sTZ: '',
  // sNewDeviceReg: '',
  //  DOW: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'PH']
};

@Component({
  selector: 'app-mqtt-status',
  templateUrl: './mqtt-status.component.html',
  styleUrls: ['./mqtt-status.component.scss']
})
export class MqttStatusComponent extends MqttService implements OnInit, OnDestroy {
  oOptions: any = {
    useSSL: MQTT_USESSL,
    userName: "test",
    password: "test12",
  };
  user: User;

  //  oSub: any = null;
  constructor(private authenticationService: AuthenticationService) {
    super(MQTT_HOST, MQTT_PORT, Global_Variables, false);
    super.setMqttOptions(this.oOptions);
    this.user = this.authenticationService.userValue;
    // this.oSub = this.authenticationService.user.subscribe(x => {
    //   this.user = x;
    // });
  }

  sMqttHost = MQTT_HOST;
  public override getMqttHost() {
    return this.sMqttHost;
  }
  public override getClientID() {
    return "admin-" + uuidv4();
    // return "admin-" + UUID.UUID();
  }

  ngOnInit() {
    super.setConnect(true);
    this.oItems.forEach((oItem) => {
      super.subscribe(oItem.Topic);
    });
  }
  ngOnDestroy() {
    console.log("ngOnDestroy")
    try {
      super.setConnect(false);
    }
    catch (err) {
    }
  }
  public oItems = [{ Topic: '$SYS/broker/clients/connected', Display: 'Connected Clients', Value: '0' }
    //      ,'$SYS/broker/clients/disconnected'
    , { Topic: '$SYS/broker/clients/total', Display: 'Connected Clients', Value: '0' }
    , { Topic: '$SYS/broker/messages/inflight', Display: 'Inflight Msg Requiring Ack', Value: '0' }
    //      ,'$SYS/broker/messages/sent'
    //      ,'$SYS/broker/uptime'
  ];

  processMessage(message: Paho.MQTT.Message) {
    var t = super.getTopicPathInArray(message);
    if (t[0] == 'ServerUp')
      return;
    if (t[0] == 'IP') {
      this.oMqttStatus = message.payloadString;
      return;
    }
    //    var m = super.getMessageInArray(message);
    this.oItems.forEach((oItem) => {
      if (oItem.Topic == message.destinationName) {
        oItem.Value = message.payloadString;
      }
    });
  }
  public override getUserName(): string {
    // if (this.oSub != null && this.user.access_token !== undefined) {
    //   this.oSub.unsubscribe();
    //   this.oSub = null;
    // }
    return this.user.access_token || '' ;
  }
}
