import { Injectable } from '@angular/core';
import { AuthenticationService } from '../../../../app/_services';
import { Role, User } from '../../../../app/_models';

@Injectable({
  providedIn: 'root'
})
export class AuthorisedSideNavService {
  hideSideNav: boolean = true;

  toggleSideNav(): void {
    this.hideSideNav = !this.hideSideNav;
  }

  user: User|undefined = undefined;
  constructor(private authenticationService: AuthenticationService) {
    //    this.user = this.authenticationService.userValue;
    this.authenticationService.user.subscribe(x => {
      this.user = x;
      localStorage.removeItem("oEntity");
      localStorage.removeItem("oService");
    });
  }

  get isQueueAdmin() {
    return this.user && this.user.TYPE === Role.QueueAdmin;
  }

  get isEntityAdmin() {
    return this.user && this.user.TYPE === Role.EntityAdmin;
  }

  logout(): void {
    this.authenticationService.logout();
  }

  ngOnInit() {

  }
}
