  sToday:any = null;

  loadWorkingHours() {
    this.oAPIService.send2ServerP("dow_today/" + this.oEntity.ID).then((data:any) => {
      if (data['_error'])
        console.error(data);
      else
        this.sToday = this.sWOD[parseInt(data.result[0]['TODAY'])];
    });
    let oFG = this.oBuilder.group({
      SU: this.oBuilder.array([]),
      MO: this.oBuilder.array([]),
      TU: this.oBuilder.array([]),
      WE: this.oBuilder.array([]),
      TH: this.oBuilder.array([]),
      FR: this.oBuilder.array([]),
      SA: this.oBuilder.array([]),
      PH: this.oBuilder.array([]),
    });
    this.oInputForm = oFG;

    this.oAPIService.send2ServerP("services/" + this.oService.ID + "/wh/fetch").then((data:any) => {
      if (data['_error'])
        console.error(data);
      else {
        this.oWorkingHours = data.result;
        //      console.log(this.oWorkingHours);
        this.populateFormData();
      }
    });
  }

