  getControls(frmGrp: FormGroup, key: string) {
    return (<FormArray>frmGrp.controls[key]).controls;
  }
  getControlArray(frmGrp: FormGroup, key: string) {
    return (<FormArray>frmGrp.controls[key]);
  }
  sWOD = ['SU', 'MO', 'TU', 'WE', 'TH', 'FR', 'SA', 'PH']
  populateFormData() {
    //    console.log("Total Defined Working Hours");
    //    console.log(this.oWorkingHours);
    this.oWorkingHours?.forEach(o => {
      let s = this.sWOD[o.DAYOFWEEK];
      const arrayControl = <FormArray>this.oInputForm.controls[s];
      let oFC = this.setFields(o);
      //      console.log("Pushing: " + s + " " + oFC);
      arrayControl.push(oFC);
    });

  }
  setFields(oData:any) {
    return this.oBuilder.group({
      OPEN_TIME: [oData.TIME_OPEN],//, [Validators.required]],
      CLOSE_TIME: [oData.TIME_CLOSE],//, [Validators.required]],
      MAXQN: [oData.MAXQN],
      EntityID: this.oEntity.ID  // Required ?
    });
  }

  initFields() { //day: FormArray = null) {
    return this.oBuilder.group({
      OPEN_TIME: ['0900'],//, [Validators.required]],
      CLOSE_TIME: ['1200'],//, [Validators.required]],
      MAXQN: ['1000'],
      EntityID: this.oEntity.ID // Required ?
    });
  }
  addInput(day: FormArray): void {
    day.push(this.initFields());
  }
  delInput(day: FormArray, index: number): void {
    day.removeAt(index);
  }
  onSubmit(): void {
    console.log(this.oInputForm?.value);
    var o = this.oInputForm?.value;
    this.oAPIService.send2ServerP("services/" + this.oEntity.ID + "/" + this.oService.ID + "/wh/delete", true, this.oService).then((data: any) => {
      this.insert(o.SU, 0);
      this.insert(o.MO, 1);
      this.insert(o.TU, 2);
      this.insert(o.WE, 3);
      this.insert(o.TH, 4);
      this.insert(o.FR, 5);
      this.insert(o.SA, 6);
      this.insert(o.PH, 7);
      this.ngOnInit();
    });
    // let alert = this.alertCtrl.create({
    //   title: 'Saved',
    //   subTitle: 'Successfully',
    //   buttons: ['Dismiss']
    // });
    // alert.present();
  }
  insert(oDay: any, iDOW: number) {
    for (let i = 0; i < oDay.length; i++) {
      let oItem = oDay[i];
      oItem['DOW'] = iDOW;
      console.log(iDOW, oDay);
      if (this.sWOD[iDOW] != this.sToday)
        this.oAPIService.send2ServerP("services/" + this.oService.ID + "/wh/add", true, oItem).then((data: any) => {
        });
      // if (i == oDay.length - 1)
      //   this.onBack();
    }
  }
