import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { GuestLayoutComponent } from './layout/guest/guest-layout/guest-layout.component';
import { LandingPageComponent } from './pages/landing-page/landing-page.component';
import { AuthorisedLayoutComponent } from './layout/authorised/authorised-layout/authorised-layout.component';
import { DashboardComponent } from './pages/dashboard/dashboard.component';
import { MqttStatusComponent } from './pages/mqtt-status/mqtt-status.component';
import { ListEntitiesComponent } from './pages/list-entities/list-entities.component';
import { ListServicesComponent } from './pages/list-services/list-services.component';
import { ListUsersComponent } from './pages/list-users/list-users.component';
import { EditEntityComponent } from './pages/edit-entity/edit-entity.component';
import { EditServiceComponent } from './pages/edit-service/edit-service.component';
import { EditUserComponent } from './pages/edit-user/edit-user.component';
import { BusinessHoursComponent } from './pages/business-hours/business-hours.component';
// import { MessageComponent } from 'angular6-json-schema-form';
import { MessagesComponent } from './pages/messages/messages.component';

const routes: Routes = [
  {
    path: '',
    component: GuestLayoutComponent,
    children: [
      { path: '', component: LandingPageComponent, pathMatch: 'full' },
    ]
  },
  {
    path: '',
    component: AuthorisedLayoutComponent,
    children: [
      { path: '', component: MessagesComponent, pathMatch: 'full' },
      { path: 'messages', component: MessagesComponent, pathMatch: 'full' },
      { path: 'dashboard', component: DashboardComponent, pathMatch: 'full' },
      { path: 'mqttstatus', component: MqttStatusComponent, pathMatch: 'full' },
      { path: 'clinics', component: ListEntitiesComponent, pathMatch: 'full' },
      { path: 'services', component: ListServicesComponent, pathMatch: 'full' },
      { path: 'users', component: ListUsersComponent, pathMatch: 'full' },
      { path: 'edit-entity', component: EditEntityComponent, pathMatch: 'full' },
      { path: 'edit-service', component: EditServiceComponent, pathMatch: 'full' },
      { path: 'edit-user', component: EditUserComponent, pathMatch: 'full' },
      { path: 'business-hours', component: BusinessHoursComponent, pathMatch: 'full' }

    ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
